import { Outlet, createBrowserRouter } from "react-router-dom";
import Page from "./Page";
import NotFound from "./app/NotFound";

const router = createBrowserRouter([
  {
    path: "/",
    element: <Outlet />,
    errorElement: <NotFound />,
    children: [
      {
        path: "",
        element: <Page />,
      },
    ],
  },
]);

export default router;
